export default {
	data() {
		return {
			dataList:[]
		}
	},
	onLoad(){
		uni.setNavigationBarColor({
			backgroundColor: "#60ACF8",
			frontColor: '#ffffff',
			animation: {
				duration: 400,
				timingFunc: 'easeIn'
			}
		})
	},
	onShow(){
		this.initData()
	},
	methods: {
		initData(){
			let self = this
			this.$acFrame.HttpService.inviteRank().then(res=>{
				if(res.success){
					self.dataList = res.data
				}
			})
		},
		setImg(src){
			return  this.$acFrame.Util.setImgUrl(src);
		},
	}
}
